.back-button {
  position: fixed;
  display: block;
  width: 90px;
  padding: 7px 5px 10px 10px;
  top: 50%;
  left: -35px;
  transform: rotate(90deg);
  color: $gray-dark;
  background-color: $second-color;
  border: 1px solid $main-color;
  border-radius: 15px 15px 0 0;
  opacity: .4;
  transition: all .5s;
  z-index: 10;

  &:hover {
    opacity: 1;
    box-shadow: 3px -2px  4px 1px $gray;
  }
}
