.assist {
  padding: 0 60px 100px;
  margin: 0 auto;
  max-width: 1160px;

  @media (max-width: $screen-xmd) {
    padding: 0 35px 100px;
  }

  &__title {
    margin-bottom: 0;
    padding-bottom: 15px;
    font-size: 46px;

    @media (max-width: 649px) {
      font-size: 36px;
    }
  }

  &__text {
    margin: 0;
    padding-bottom: 80px;
    font-style: italic;
  }
}
